.head_container {
  .header {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    padding-top: 16px;
    padding-bottom: 16px;
    z-index: 1000;

    &__container {
      @include flex-all-sb;
    }
  }
}

.header {
  left: 0;
  top: 0;
  width: 100%;
  padding-top: 8px;
  padding-bottom: 8px;
  z-index: 1000;

  &__container {
    @include flex-all-sb;
  }

  .nav .button__empty {
    display: none;
  }
}

.header_shadow .header {
  box-shadow: 0px 4px 20px rgba(35, 31, 32, 0.15);
  background-color: var(--light-color);
  position: relative;
  z-index: 1000;
}

