.block_preview {
  width: 100%;
  height: 570px;
  @include bg-cover;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding-left: 40px;
  padding-bottom: 40px;
  margin-bottom: 100px;
  display: flex;
  position: relative;
  transition: transform 0.3s ease-in-out;

  &__subname {
    font-weight: 600;
    font-size: 32px;
    line-height: 40px;
    color: var(--light-color);
    margin-bottom: 16px;
  }

  &__text {
    font-size: 16px;
    line-height: 24px;
    color: var(--light-color);
    margin-bottom: 32px;
  }

  .button {
    max-width: 134px;
    padding: 12px 24px;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
  }

  &__button {
    &:hover {
      background-color: var(--light-color);
      color: var(--red-color);
      border: 1px solid var(--light-color);
    }
  }

  a {
    display: block;
  }

  &__link {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
  }

  &__inner {
    position: relative;
    z-index: 1;
  }

  &:hover {
    transform: scale(1.02);
  }
}
